import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import PA1 from 'assets/images/PA/policypal-personal-accident-insurance-1.svg';
import PA2 from 'assets/images/PA/policypal-personal-accident-insurance-2.svg';
import PA3 from 'assets/images/PA/policypal-personal-accident-insurance-3.svg';

import PAEditDetail from './PAEditDetail';
import FormLanding from '../Templates/FormLanding/FormLanding';

import './PA.scss';

class PAMain extends Component {
  render() {
    const { paFields, paPromoGiveAway } = this.props;

    return (
      <div className="pa-main-container">
        <FormLanding
          img1={PA1}
          img2={PA2}
          img3={PA3}
          imgAlt="PolicyPal Personal Accident Insurance"
          promo={paPromoGiveAway}
          desc={
            <Fragment>
              <p>Get 5% Cashback upon purchase of</p>
              <p>Personal Accident Insurance!</p>
            </Fragment>
          }
        >
          <PAEditDetail
            paFields={paFields}
          />
        </FormLanding>
      </div>
    );
  }
}

PAMain.defaultProps = {
  paPromoGiveAway: null,
};

PAMain.propTypes = {
  paFields: PropTypes.object.isRequired,
  paPromoGiveAway: PropTypes.object,
};

export default PAMain;
