/**
 * PAMainContainer.js
 * The Main container for PA
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

import PAMain from '../../components/PA/PAMain';

import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import { getPAFields, clearPAData } from '../../actions/paActions';
import { setLoading, clearLoading } from '../../actions/loadingScreenActions';
import { gaTag, ampTag } from '../../lib/tagFunctions';

class PAMainContainer extends Component {
  constructor(props) {
    super(props);

    const {
      loading,
      paFields,
      history,
      clearData,
    } = this.props;

    clearData();

    if (!paFields) {
      loading(() => history.push('/'));
      this.props.getPAFields();
    }
  }

  componentDidMount() {
    const { setFormState, history } = this.props;
    setFormState(FORMSTATES.MAIN.index);
    window.scrollTo(0, 0);

    gaTag({
      page_title: 'PersonalAccident',
      page_path: `/personal-accident${history.location.search}`,
    });
    ampTag('PAFormS');
  }

  componentDidUpdate(prevProps) {
    const { paFields, loadingEnd } = this.props;
    if (!prevProps.paFields && paFields) {
      loadingEnd();
    }
  }

  render() {
    const { paFields, paPromoGiveAway } = this.props;

    if (!paFields) {
      return null;
    }

    return (
      <PAMain
        paFields={paFields}
        paPromoGiveAway={paPromoGiveAway}
      />
    );
  }
}

PAMainContainer.defaultProps = {
  paFields: null,
  paPromoGiveAway: null,
};

PAMainContainer.propTypes = {
  paPromoGiveAway: PropTypes.object,
  paFields: PropTypes.object,
  getPAFields: PropTypes.func.isRequired,
  loading: PropTypes.func.isRequired,
  loadingEnd: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
  clearData: PropTypes.func.isRequired,
  setFormState: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  paPromoGiveAway: state.insurance.promoGiveaways.pa,
  paFields: state.pa.paFields,
});

const mapDispatchToProps = dispatch => ({
  getPAFields: () => {
    dispatch(getPAFields());
  },
  loading: (callBack, errorMsg, time) => {
    dispatch(setLoading(callBack, errorMsg, time));
  },
  loadingEnd: () => {
    dispatch(clearLoading());
  },
  clearData: () => {
    dispatch(clearPAData());
  },
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(PAMainContainer));
